.c-portfolio-holder {
    padding-left: 20px;
    padding-right: 20px;
    @include span(4 of 12);
    @media (max-width: $break-tablet) {
      @include span(12 of 12);
    }
}
.c-portfolio-item{
  cursor: pointer;
  position: relative;
  list-style: none;
  padding: gutter() 0;
  transition: .2s all ease-out;
  a {
    display: block;
  }
  img{
    width: 100%;
    height: auto;
  }

  &.active {
   .o-overlay {
     z-index: 2;
     background-color: rgba(0, 0, 0, .75);
   }
    img {
      filter: grayscale(100%);
    }
    .c-portfolio-info {
      opacity: 1;
    }
  }
  .c-portfolio-item__wrapper {
    position: relative;
  }
}


.c-portfolio-holder-column1 {
  @media(min-width: $break-tablet){
    padding-left: ($base-spacing-unit * 2);
  }
}
.c-portfolio-holder-column3 {
  @media(min-width: 401px){
  padding-right: 40px;
}
  @media(max-width: 400px){
    padding-left: 10px;
  }
}

.c-portfolio-info {
  position: absolute;
  top: 50%;
  overflow: hidden;
  opacity: 0;
  transition: max-height .3s , opacity .8s ease;
  text-align: center;
  padding: 0 $base-spacing-unit;
  transform: translateY(-50%);
  text-align: center;
  width: 100%;
  z-index: 5;

  .c-portfolio-info__caption, .c-portfolio-info__description {
    color: $color-white;
  }

}

.c-portfolio-info__caption {
  @include header-font();
   font-size: 20px;
   padding-top: ($base-spacing-unit + 10 );
   line-height: 1em;
   margin-bottom: $small-spacing-unit;
}

// Adding underline to animate after info
.c-portfolio-info__description {
  position: relative;
  padding-bottom: ($med-spacing-unit + 3); // 3px for the thickness of the underline
  display: inline-block;


  // &::before {
  //   content: "";
  //   position: absolute;
  //   visibility: hidden;
  //   transform: scaleX(0);
  //   transition: all .3s ease-out;
  //   transition-delay: .3s;
  //   width: 100%;
  //   bottom: 0;
  //   left: 0;
  //   border-bottom: 3px solid $color-brand-primary;
  // }
}
.c-portfolio-info__underline {
  position: absolute;
  width: 75%;
  height: 3px;
  bottom: 0;
  left: 12.5%;
  background: $color-brand-primary;
  transform: scaleX(0);
  transition: all .3s ease-out;
  transition-delay: .3s;
}

// Using Hover class that is added by js on hover to animate underline
.c-portfolio-item.active {
  .c-portfolio-info__underline {
  visibility: visible;
  transform: scaleX(1);
 }
}

// Fade out portfolio not in focus
.c-portfolio-item--not-active {
  img {
    // filter: opacity(.4) brightness(70%);
  }
}

/******** Single portfolio page ********/

// Adding underline to single portfolio info
.c-portfolio-item--single {
  text-align: center;
  max-width: 100%;
  font-size: 15px;
    .c-portfolio-info__caption{
      font-size: 24px;
    }
    .c-portfolio-info__underline {
      visibility: visible;
      transform: scaleX(1);
    }
  .c-portfolio-info__description {
    padding-bottom: ($base-spacing-unit + 3); // add 3px for thickness of underline
    @include nav-font();

  }

}

.c-portfolio__content {
  @include body-font();
  // padding-top: $base-spacing-unit;
}

.c-portfolio__separator {
  width: 50%;
  margin: 0 auto;
  border-top: 1px solid $color-brand-secondary;
  padding-top: $base-spacing-unit *1.5;
  opacity: .5;
  margin-top: $base-spacing-unit *3;
}
